import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const modal = document.querySelector('.backdrop-order');
const closeBtn = document.querySelector('.modal-order-close');
const form = document.querySelector('.modal-form');

// –§—É–Ω–∫—Ü—ñ—è —Å–∫–∏–¥–∞–Ω–Ω—è —Ñ–æ—Ä–º–∏ —Ç–∞ –ø–æ–º–∏–ª–æ–∫
function resetForm() {
  form.reset();
  form.querySelectorAll('.error').forEach(input => {
    input.classList.remove('error');
  });
}

// –ó–∞–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞
closeBtn.addEventListener('click', () => {
  modal.style.display = 'none';
  document.body.classList.remove('modal-open'); // –†–æ–∑–±–ª–æ–∫—É–≤–∞–Ω–Ω—è —Å–∫—Ä–æ–ª—É
  resetForm();
});

// –û–±—Ä–æ–±–∫–∞ —Ñ–æ—Ä–º–∏
form.addEventListener('submit', e => {
  e.preventDefault();

  const nameInput = form.querySelector('[name="name"]');
  const phoneInput = form.querySelector('[name="phone"]');
  const commentInput = form.querySelector('[name="comment"]');

  // –ó–Ω—ñ–º–∞—î–º–æ —Å—Ç–∞—Ä—ñ –ø–æ–º–∏–ª–∫–∏
  nameInput.classList.remove('error');
  phoneInput.classList.remove('error');

  const name = nameInput.value.trim();
  const phone = phoneInput.value.trim();

  // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –æ–±–æ–≤'—è–∑–∫–æ–≤–∏—Ö –ø–æ–ª—ñ–≤
  if (!name || !phone) {
    if (!name) nameInput.classList.add('error');
    if (!phone) phoneInput.classList.add('error');

    iziToast.error({
      message: "–ë—É–¥—å –ª–∞—Å–∫–∞, –∑–∞–ø–æ–≤–Ω—ñ—Ç—å —ñ–º'—è —Ç–∞ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É!",
      position: 'topRight',
      timeout: 3000,
      zindex: 999999,
      appendTo: document.body,
    });
    return;
  }

  console.log('–í—ñ–¥–ø—Ä–∞–≤–ª—è—î–º–æ:', {
    name,
    phone,
    comment: commentInput.value,
  });

  resetForm();
  modal.style.display = 'none';
  document.body.classList.remove('modal-open');

  iziToast.success({
    title: '–£—Å–ø—ñ—Ö',
    message: '–í–∞—à–∞ –∑–∞—è–≤–∫–∞ –Ω–∞–¥—ñ—Å–ª–∞–Ω–∞!',
    position: 'topRight',
    timeout: 3000,
    zindex: 999999,
    appendTo: document.body,
  });
});

// –í–∏–¥–∞–ª–µ–Ω–Ω—è —á–µ—Ä–≤–æ–Ω–æ—ó —Ä–∞–º–∫–∏ –ø—Ä–∏ –≤–≤–µ–¥–µ–Ω–Ω—ñ
form.querySelectorAll('input, textarea').forEach(input => {
  input.addEventListener('input', () => {
    if (input.value.trim()) {
      input.classList.remove('error');
    }
  });
});
