import Swiper from 'swiper';
import { Navigation, Pagination } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { getFeedbacks } from './stories.api';
import { showLoader, hideLoader } from './loader';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

import 'rateyo/lib/cjs/rateyo.css';
import RateYo from 'rateyo';

const cardStoryEl = document.querySelector('.swiper-wrapper-story');
let mySwiper = null;

function creatStoriesCard(events) {
  const markup = events
    .map(({ rate, description, author }) => {
      return `<div class="swiper-slide swiper-slide-story" role="listitem">
                <div class="story-card">
                   <div class="story-rating" data-rate="${rate}"></div>
                  <p class="story-review">${description}</p>
                  <p class="story-author">${author}</p>
                </div>
              </div>`;
    })
    .join('');

  cardStoryEl.innerHTML = markup;
}

function clamp(num, min, max) {
  return Math.min(Math.max(num, min), max);
}

function renderStars() {
  const ratings = document.querySelectorAll('.story-rating');

  if (!ratings || ratings.length === 0) return;

  ratings.forEach(el => {
    if (el.classList.contains('jq-ry-container')) return;

    const raw = el.dataset.rate;

    let rate = Number(String(raw).replace(',', '.'));

    if (Number.isNaN(rate)) rate = 0;
    if (rate < 0) rate = 0;
    if (rate > 5) rate = 5;

    try {
      new RateYo(el, {
        rating: rate,
        starWidth: '21px',
        halfStar: true,
        precision: 2,
        readOnly: true,
        normalFill: '#E0E0E0',
        ratedFill: '#F6C200',
        spacing: '6px',
      });

      el.setAttribute('role', 'img');
      el.setAttribute('aria-label', `–†–µ–π—Ç–∏–Ω–≥ ${rate} –∑ 5`);
    } catch (err) {
      console.error('‚ùå RateYo init failed', el, err);
    }
  });
}

function initSwiper() {
  mySwiper = new Swiper('.swiper-story', {
    modules: [Navigation, Pagination],
    slidesPerView: 1,
    spaceBetween: 32,

    wrapperClass: 'swiper-wrapper-story',
    slideClass: 'swiper-slide-story',

    navigation: {
      nextEl: '.story-button-next',
      prevEl: '.story-button-prev',
    },

    pagination: {
      el: '.swiper-pagination-story',
      clickable: true,
      dynamicBullets: true,
    },

    breakpoints: {
      768: {
        slidesPerView: 2,
      },
      1440: {
        slidesPerView: 2,
      },
    },
  });
  return mySwiper;
}
function showError(message) {
  iziToast.error({
    title: '–ü–æ–º–∏–ª–∫–∞',
    message: message,
    position: 'topRight',
    timeout: 5000,
    closeOnClick: true,
    pauseOnHover: true,
    progressBar: true,
  });
}

async function initStories() {
  try {
    showLoader();

    const data = await getFeedbacks();

    if (data && data.feedbacks && data.feedbacks.length > 0) {
      if (data.feedbacks.length < 3) {
        showError('–ù–µ–¥–æ—Å—Ç–∞—Ç–Ω—å–æ –≤—ñ–¥–≥—É–∫—ñ–≤ –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è');
        return;
      }

      creatStoriesCard(data.feedbacks);
      renderStars();
      initSwiper();
    } else {
      showError('–ù–µ –≤–¥–∞–ª–æ—Å—è –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ –≤—ñ–¥–≥—É–∫–∏');
    }
  } catch (error) {
    showError('–°—Ç–∞–ª–∞—Å—è –ø–æ–º–∏–ª–∫–∞ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ –≤—ñ–¥–≥—É–∫—ñ–≤');
  } finally {
    hideLoader();
  }
}

initStories();
