import axios from 'axios';

const BASE_URL = 'https://paw-hut.b.goit.study/api';
axios.defaults.baseURL = BASE_URL;

export async function getFeedbacks() {
  try {
    const { data } = await axios.get('/feedbacks', {
      params: {
        page: 1,
        limit: 15,
      },
    });
    return data;
  } catch (error) {
    if (error.response) {
      const message =
        error.response.data?.message ||
        `–ü–æ–º–∏–ª–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞: ${error.response.status}`;
      throw new Error(message);
    } else if (error.request) {
      throw new Error(
        "–ù–µ –≤–¥–∞–ª–æ—Å—è –∑'—î–¥–Ω–∞—Ç–∏—Å—è –∑ —Å–µ—Ä–≤–µ—Ä–æ–º. –ü–µ—Ä–µ–≤—ñ—Ä—Ç–µ —ñ–Ω—Ç–µ—Ä–Ω–µ—Ç-–∑'—î–¥–Ω–∞–Ω–Ω—è"
      );
    } else {
      throw new Error('–ü–æ–º–∏–ª–∫–∞ –ø—Ä–∏ –≤—ñ–¥–ø—Ä–∞–≤—Ü—ñ –∑–∞–ø–∏—Ç—É');
    }
  }
}
